body{
	margin-top: 80px;
	font-family: 'Roboto';
}

@media print {
  body {
    font-size: 11px;
    line-height: 9px;
  }
  .col-md-6,.col-md-3{
  	padding-left: .4em;
  	padding-right:.4em;
  }
  .dont-break { page-break-inside:avoid; } 

  .container{
    margin-top: -250px;
  }
  .fbm-print-remove{
    display: none;
  }
}
